import erp from "../../assets/img/erp.png";

const Index = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="col-sm-2">
          {" "}
          <img
            src={erp}
            className="img-fluid"
            alt="logo"
            style={{ width: 120, height: 90 }}
          />
        </div>
        <div className="col-sm-10">
          <h3>
            <strong>ERP</strong> (Enterprise Resource Planning)
          </h3>
          <p>
            Un sistema <strong>ERP</strong>, tambin conocido como sistema de
            planificacin de recursos empresariales,es un software que integra
            diferentes reas de una empresa para optimizar la gestin y el flujo
            de informacin.Permite centralizar la gestin de recursos, procesos
            y datos de la empresa, mejorando la eficiencia y el control.
          </p>
        </div>
        <div className="row">
          <h3 className="text-center text-muted">
            <strong>Caractersticas de un sistema ERP</strong>
          </h3>
          <br />
          <h5>
            <strong> Centralizacin de la informacin:</strong>
          </h5>
          <p>
            Un ERP recopila y centraliza datos de todos los departamentos,
            creando una nica fuente de informacin.
            <br />
            <strong>Automatizacin de procesos:</strong> &nbsp; El ERP
            automatiza tareas repetitivas y flujos de trabajo, reduciendo
            errores y ahorrando tiempo.
            <br />
            <strong>Gestin integrada:</strong> &nbsp; Integra diferentes reas
            como finanzas, ventas, logstica, produccin, recursos humanos,
            entre otras.
            <br />
            <strong>Informes y anlisis:</strong> &nbsp; Proporciona
            herramientas para generar informes y anlisis de datos, facilitando
            la toma de decisiones.
            <br />
            <strong>Escalabilidad:</strong> &nbsp; Los sistemas ERP pueden
            adaptarse a las necesidades de crecimiento de la empresa <br />
            <strong> Disponibilidad:</strong> &nbsp; Pueden estar disponibles en
            la nube (SaaS) o en local (on-premise).
            <br />
            <strong>Ventajas de un sistema ERP:</strong> &nbsp; Mejora la
            eficiencia: Automatiza procesos, reduce errores y mejora la
            productividad.
            <br />
            <strong>Optimiza la gestin de recursos:</strong> &nbsp; Permite un
            mejor control del inventario, los recursos humanos y los procesos
            financieros.
            <br />
            <strong> Facilita la toma de decisiones:</strong> &nbsp; Proporciona
            informacin precisa y actualizada para una mejor planificacin y
            anlisis.
            <br />
            <strong>Mejora la comunicacin:</strong> &nbsp; Centraliza la
            informacin y facilita la comunicacin entre departamentos.
            <br />
            <strong>Reduccin de costos:</strong> &nbsp; Optimiza los procesos,
            reduce errores y permite una mejor gestin de los recursos, lo que
            se traduce en una reduccin de costos.
          </p>
          <h5>
            <strong>En resumen:</strong>
          </h5>{" "}
          <p>
            Un sistema ERP es una herramienta fundamental para cualquier empresa
            que busque mejorar la gestin de sus procesos y la eficiencia de sus
            operaciones.
          </p>
        </div>
      </div>
    </div>
  );
};
export default Index;
